import React, { Suspense, useEffect, useState } from "react";
import { BrowserRouter, Navigate, Route, Routes } from "react-router-dom";
import AppDrawer from "./layout/drawer/AppDrawer";
import Header from "./layout/header/Header";
import { serviceKey } from "./shared/constants/Constants";
import { routes } from "./routes";
// import * as PusherPushNotifications from "@pusher/push-notifications-web";
import OneSignal from "react-onesignal";
import "./App.scss";

function App() {
  const [isDrawerOpen, setIsDrawerOpen] = useState({
    left: false,
  });

  useEffect(() => {
    OneSignal.init({
      appId: serviceKey.ONESIGNAL_APPID,
    }).then(() => {
      OneSignal.sendTag("tech", "react").then(() => console.log("Works"));
    });
  }, []);

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }
    setIsDrawerOpen({ ...isDrawerOpen, [anchor]: open });
  };

  const createRoutes = (routesArray) => {
    return routesArray?.map(({ path, component }) => (
      <Route key={path} path={path} element={component} exact />
    ));
  };

  // const beamsClient = new PusherPushNotifications.Client({
  //   instanceId: serviceKey.BEAM_INSTANCE_ID,
  // });
  // console.log("inside the pusher");
  // beamsClient
  //   .start()
  //   .then(() => beamsClient.addDeviceInterest("hello"))
  //   .then(() => console.log("Successfully registered and subscribed!"))
  //   .catch(console.error);

  return (
    <div className="App">
      <BrowserRouter>
        <AppDrawer
          isDrawerOpen={isDrawerOpen}
          toggleDrawer={toggleDrawer}
        ></AppDrawer>
        <Suspense fallback={""}>
          <Header toggleDrawer={toggleDrawer} />

          <main>
            <div className="main-content">
              <div>
                <Suspense fallback={""}>
                  <Routes>
                    {createRoutes(routes)}
                    <Route path="*" element={<Navigate to="/" replace />} />
                  </Routes>
                </Suspense>
              </div>
            </div>
          </main>
        </Suspense>
      </BrowserRouter>
    </div>
  );
}

export default App;
