import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import Box from "@mui/material/Box";
import "./Tabs.scss";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      className="tabPanel-tab-header"
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 1 }}>
          <div>{children}</div>
        </Box>
      )}
    </div>
  );
}

function TabComponent({ tabData }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: "100%" }} className="tabComponent-container">
      <Box className="tabComponent-box">
        <Tabs
          value={value}
          onChange={handleChange}
          aria-label="basic tabs example"
          variant="fullWidth"
          className="tabComponent-tabs"
        >
          {tabData.map((tabItem, index) => {
            return (
              <Tab
                key={tabItem?.label}
                label={tabItem?.label}
                id={`simple-tab-${index}`}
                className="tabComponent-tab"
                aria-controls={`simple-tabpanel-${index}`}
              />
            );
          })}
        </Tabs>
      </Box>
      {tabData.map((tabItem, index) => {
        return (
          <TabPanel key={tabItem?.label} value={value} index={index}>
            {tabItem?.component}
          </TabPanel>
        );
      })}
    </Box>
  );
}

export default TabComponent;
