import { useState } from "react";
import {
  Card,
  CardContent,
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
  FormLabel,
  Button,
} from "@mui/material";

function Questions({ question }) {
  const [value, setValue] = useState("");

  const handleRadioChange = (event) => {
    setValue(event.target.value);
  };

  return (
    <Card className="question" sx={{ maxWidth: 345 }}>
      <CardContent>
        <div>
          <FormControl sx={{ m: 3 }} variant="standard">
            <FormLabel id="demo-error-radios">
              <h4>{question.question}</h4>
            </FormLabel>
            <RadioGroup
              aria-labelledby="demo-error-radios"
              name="quiz"
              value={value}
              onChange={handleRadioChange}
            >
              {question?.answers.map((result, index) => (
                <FormControlLabel
                  key={index}
                  value={result}
                  control={<Radio color="error" />}
                  label={result}
                />
              ))}
            </RadioGroup>
            <Button
              className="button-outline"
              sx={{ mt: 1, mr: 10 }}
              variant="outlined"
              size="small"
            >
              Save
            </Button>
          </FormControl>
        </div>
      </CardContent>
    </Card>
  );
}

export default Questions;
